
"use client";

import { useState, useMemo, useEffect } from "react";
import { useRouter, useSearchParams } from "next/navigation";
import { Card, CardContent, CardDescription, CardFooter, CardHeader, CardTitle } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";
import { Loader2, Shuffle, CheckCircle, XCircle, Trophy, Save, ArrowRight, ArrowLeft } from "lucide-react";
import { cn } from "@/lib/utils";
import type { GenerateEducationalGameOutput } from "@/ai/flows/educational-game-generation";
import { useToast } from "@/hooks/use-toast";
import { Input } from "@/components/ui/input";
import { Accordion, AccordionContent, AccordionItem, AccordionTrigger } from "@/components/ui/accordion";
import { Progress } from "@/components/ui/progress";

interface GameClientProps {
  initialGameData: GenerateEducationalGameOutput | null;
  initialGrade?: number;
  initialTopic?: string;
  initialGameType?: string;
}

const gameTypes = [
  { id: 'Quiz', name: 'Quiz', description: 'Multiple choice questions' },
  { id: 'Bingo', name: 'Bingo Cards', description: 'Educational bingo with terms' },
  { id: 'True/False', name: 'True/False Quiz', description: 'True or false statements' },
  { id: 'Fill-in-the-blanks', name: 'Fill in the Blanks', description: 'Complete sentences with missing words' },
  { id: 'Quick Draw', name: 'Quick Draw', description: 'Draw and guess concepts' },
  { id: 'Word Association', name: 'Word Association', description: 'Related words to topic' },
  { id: 'Sorting', name: 'Sorting Game', description: 'Categorize items into groups' },
  { id: 'Riddles', name: 'Riddles', description: 'Educational riddles with answers' },
  { id: 'Scrambled Words', name: 'Scrambled Words', description: 'Unscramble key terms' },
  { id: 'Odd One Out', name: 'Odd One Out', description: 'Find which item doesn\'t belong' },
  { id: 'Quick Questions', name: 'Quick Questions', description: 'Rapid-fire yes/no questions' }
];

// Generic component to display game data as JSON. Used as a placeholder.
const GenericGameDisplay = ({ content, gameType }: { content: any; gameType: string }) => {
    return (
        <div>
            <h3 className="font-bold text-lg mb-2">{gameType}</h3>
            <pre className="whitespace-pre-wrap font-body text-sm leading-relaxed bg-muted p-4 rounded-md">
                {JSON.stringify(content, null, 2)}
            </pre>
        </div>
    );
}

type QuizQuestion = {
    question: string;
    options: string[];
    answer: string;
};

// A fully implemented Quiz game component
const QuizDisplay = ({ data }: { data: { questions: QuizQuestion[] }}) => {
    const [currentQuestionIndex, setCurrentQuestionIndex] = useState(0);
    const [selectedAnswers, setSelectedAnswers] = useState<Record<number, string>>({});
    const [showResults, setShowResults] = useState(false);

    const questions = data.questions || [];
    const currentQuestion = questions[currentQuestionIndex];
    
    const handleAnswerSelect = (option: string) => {
        if (showResults) return;
        setSelectedAnswers(prev => ({ ...prev, [currentQuestionIndex]: option }));
    };

    const handleNext = () => {
        if (currentQuestionIndex < questions.length - 1) {
            setCurrentQuestionIndex(prev => prev + 1);
        }
    };

    const handlePrevious = () => {
        if (currentQuestionIndex > 0) {
            setCurrentQuestionIndex(prev => prev - 1);
        }
    };

    const handleSubmit = () => {
        setShowResults(true);
    };
    
    const handleRestart = () => {
        setCurrentQuestionIndex(0);
        setSelectedAnswers({});
        setShowResults(false);
    }

    const score = useMemo(() => {
        return questions.reduce((total, question, index) => {
            return total + (selectedAnswers[index] === question.answer ? 1 : 0);
        }, 0);
    }, [selectedAnswers, questions]);

    if (!questions.length) {
        return <GenericGameDisplay content={data} gameType="Quiz" />;
    }

    if (showResults) {
        return (
            <div className="flex flex-col items-center justify-center gap-4 text-center p-8 bg-accent/10 rounded-lg">
                <Trophy className="w-16 h-16 text-yellow-400" />
                <h2 className="text-2xl font-bold font-headline">Quiz Complete!</h2>
                <p className="text-muted-foreground text-lg">You scored <span className="font-bold text-foreground">{score}</span> out of <span className="font-bold text-foreground">{questions.length}</span>.</p>
                <Accordion type="single" collapsible className="w-full text-left">
                  <AccordionItem value="item-1">
                    <AccordionTrigger>Review Answers</AccordionTrigger>
                    <AccordionContent>
                      <ul className="space-y-4">
                        {questions.map((q, i) => (
                           <li key={i} className="p-3 rounded-md bg-muted/50 border">
                             <p className="font-semibold">{i+1}. {q.question}</p>
                             <p className={cn("mt-1", selectedAnswers[i] === q.answer ? 'text-green-600' : 'text-red-600')}>
                                Your answer: {selectedAnswers[i] || "Not answered"}
                             </p>
                              <p className="text-green-700">Correct answer: {q.answer}</p>
                           </li>
                        ))}
                      </ul>
                    </AccordionContent>
                  </AccordionItem>
                </Accordion>
                <Button onClick={handleRestart} className="mt-4"><Shuffle className="mr-2 h-4 w-4"/> Play Again</Button>
            </div>
        )
    }

    return (
        <div className="space-y-6">
            <div className="flex justify-between items-center">
                <p className="text-sm text-muted-foreground">Question {currentQuestionIndex + 1} of {questions.length}</p>
                <Progress value={((currentQuestionIndex + 1) / questions.length) * 100} className="w-1/2" />
            </div>
            <Card className="bg-muted/30">
                <CardHeader>
                    <CardTitle className="font-headline text-xl">{currentQuestion.question}</CardTitle>
                </CardHeader>
                <CardContent className="grid grid-cols-1 sm:grid-cols-2 gap-4">
                    {currentQuestion.options.map((option, index) => {
                        const isSelected = selectedAnswers[currentQuestionIndex] === option;
                        return (
                            <Button
                                key={index}
                                variant={isSelected ? "default" : "outline"}
                                className="w-full justify-start text-left whitespace-normal min-h-[3rem] text-base p-4"
                                onClick={() => handleAnswerSelect(option)}
                            >
                                <span className={cn("mr-4 h-6 w-6 rounded-full border flex items-center justify-center", isSelected && "bg-primary-foreground text-primary")}>
                                    {String.fromCharCode(65 + index)}
                                </span>
                                {option}
                            </Button>
                        )
                    })}
                </CardContent>
            </Card>
            <div className="flex justify-between items-center">
                <Button variant="outline" onClick={handlePrevious} disabled={currentQuestionIndex === 0}><ArrowLeft className="mr-2"/> Previous</Button>
                {currentQuestionIndex < questions.length - 1 ? (
                    <Button onClick={handleNext}>Next <ArrowRight className="ml-2"/></Button>
                ) : (
                    <Button onClick={handleSubmit} disabled={Object.keys(selectedAnswers).length !== questions.length}>Submit Quiz</Button>
                )}
            </div>
        </div>
    );
};


const GameDisplay = ({ gameData }: { gameData: GenerateEducationalGameOutput['game']}) => {
    const gameType = gameData.type.toLowerCase().replace(/ /g, '');
    const content = gameData.content;
    
    switch (gameType) {
        case 'quiz':
            return <QuizDisplay data={content} />;
        case 'bingo':
            return <GenericGameDisplay content={content} gameType="Bingo" />;
        case 'true/false':
        case 'truefalse':
            return <GenericGameDisplay content={content} gameType="True/False" />;
        case 'fill-in-the-blanks':
        case 'fillintheblanks':
            return <GenericGameDisplay content={content} gameType="Fill in the Blanks" />;
        case 'quickdraw':
            return <GenericGameDisplay content={content} gameType="Quick Draw" />;
        case 'wordassociation':
            return <GenericGameDisplay content={content} gameType="Word Association" />;
        case 'sorting':
            return <GenericGameDisplay content={content} gameType="Sorting" />;
        case 'riddles':
            return <GenericGameDisplay content={content} gameType="Riddles" />;
        case 'scrambledwords':
            return <GenericGameDisplay content={content} gameType="Scrambled Words" />;
        case 'oddoneout':
            return <GenericGameDisplay content={content} gameType="Odd One Out" />;
        case 'quickquestions':
            return <GenericGameDisplay content={content} gameType="Quick Questions" />;
        default:
            return <GenericGameDisplay content={gameData} gameType="Game" />;
    }
}


export function GameClient({ initialGameData, initialGrade, initialTopic, initialGameType }: GameClientProps) {
  const router = useRouter();
  const searchParams = useSearchParams();
  const { toast } = useToast();
  const [pending, setPending] = useState(false);
  const [grade, setGrade] = useState<string>(initialGrade?.toString() ?? "");
  const [topic, setTopic] = useState<string>(initialTopic ?? "");
  const [gameType, setGameType] = useState<string>(initialGameType ?? "");
  
  const [gameData, setGameData] = useState(initialGameData);

  useEffect(() => {
    if (initialGameData) {
      setPending(false);
    }
    setGameData(initialGameData);
  }, [initialGameData]);

  const handleGenerate = () => {
    if (grade && topic && gameType) {
      setPending(true);
      const params = new URLSearchParams();
      params.set('grade', grade);
      params.set('topic', topic);
      params.set('gameType', gameType);
      router.push(`/games?${params.toString()}`);
    }
  };
  
  const handleSave = () => {
    toast({ title: "Saved to My Library (Not Implemented)" });
  };
  
  const renderGameContent = () => {
    if (!gameData) return null;
    return <GameDisplay gameData={gameData.game} />;
  }

  return (
    <>
      <Card>
        <CardHeader>
          <CardTitle className="font-headline">Game Setup</CardTitle>
          <CardDescription>Select a grade, topic, and game type to generate a new educational game.</CardDescription>
        </CardHeader>
        <CardContent>
          <div className="flex flex-col sm:flex-row items-end gap-2">
             <div className="grid w-full max-w-xs gap-1.5">
              <label htmlFor="grade-select">Target Grade Level</label>
              <Select value={grade} onValueChange={setGrade}>
                <SelectTrigger id="grade-select">
                  <SelectValue placeholder="Select Grade" />
                </SelectTrigger>
                <SelectContent>
                  {[...Array(10)].map((_, i) => (
                    <SelectItem key={i + 1} value={(i + 1).toString()}>Grade {i + 1}</SelectItem>
                  ))}
                </SelectContent>
              </Select>
            </div>
             <div className="grid w-full max-w-xs gap-1.5">
               <label htmlFor="topic-input">Topic</label>
               <Input id="topic-input" placeholder="e.g., Animals, Space" value={topic} onChange={(e) => setTopic(e.target.value)} />
            </div>
             <div className="grid w-full max-w-xs gap-1.5">
                <label htmlFor="game-type-select">Game Type</label>
                <Select value={gameType} onValueChange={setGameType}>
                    <SelectTrigger id="game-type-select">
                        <SelectValue placeholder="Select Game Type" />
                    </SelectTrigger>
                    <SelectContent>
                        {gameTypes.map((type) => (
                            <SelectItem key={type.id} value={type.id}>{type.name}</SelectItem>
                        ))}
                    </SelectContent>
                </Select>
             </div>
            <Button onClick={handleGenerate} disabled={!grade || !topic || !gameType || pending}>
              {pending && <Loader2 className="mr-2 h-4 w-4 animate-spin" />}
              Generate Game
            </Button>
          </div>
        </CardContent>
      </Card>

      {pending && !gameData && (
        <Card>
          <CardHeader>
            <CardTitle className="font-headline flex items-center gap-2">
              <Loader2 className="h-5 w-5 animate-spin text-primary" />
              <span>Creating your game...</span>
            </CardTitle>
          </CardHeader>
           <CardContent>
            <div className="space-y-2">
               <div className="h-8 bg-muted rounded animate-pulse w-full"></div>
               <div className="h-8 bg-muted rounded animate-pulse w-5/6"></div>
            </div>
          </CardContent>
        </Card>
      )}

      {gameData && (
        <Card>
            <CardHeader>
                <div className="flex justify-between items-center">
                    <div>
                        <CardTitle className="font-headline">{gameData.game.title}</CardTitle>
                        <CardDescription>{gameData.game.instructions}</CardDescription>
                    </div>
                    {/* The shuffle button may not apply to all games, could be conditional */}
                </div>
            </CardHeader>
            <CardContent>
               {renderGameContent()}
            </CardContent>
            <CardFooter>
                 <Button variant="outline" onClick={handleSave}><Save className="mr-2" /> Save Game Content</Button>
            </CardFooter>
        </Card>
      )}
    </>
  );
}
